/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 18px;
    margin: 0;
    color: #fff;
    font-family: "Amatic SC", sans-serif;
    font-size: 25px;
    font-weight: 700;
    line-height: 1em;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    transform: translateZ(0);
}

@mixin mkdfWooCommerceSaleStyle() {
    @include mkdfSVGbg( $color: #d8cccb );
    left: -10px;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    @include mkdfSVGbg( $color: #5e3f47 );
    left: -10px;
}

@mixin mkdfWooCommerceNewProductStyle() {
    @include mkdfSVGbg( $color: #aedde6 );
    right: -10px;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 0;
    font-family: 'Nunito', sans-serif;
    font-size: 21px;
    line-height: 1;
    color: $first-main-color;
    font-weight: 900;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 3px 0 0;
        color: #bebebe;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 18px;
    line-height: 1;
    letter-spacing: 5.5px;
    color: #ffc72c;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: 2;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        white-space: nowrap;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */