/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .mkdf-onsale,
    .mkdf-sold,
    .mkdf-new-product {
        @include mkdfWooCommerceMarksGeneralStyle();
    }
    
    .mkdf-onsale {
        @include mkdfWooCommerceSaleStyle();

        .mkdf-onsale-text {
            position: relative;
        }

        .mkdf-onsale-svg {
            display: inline-block;
            position: relative;
            padding: 1px 22px 3px 18px;
        }
    }
    
    .mkdf-sold {
        @include mkdfWooCommerceOutOfStockStyle();

        .mkdf-sold-text {
            position: relative;
        }

        .mkdf-sold-svg {
            display: inline-block;
            position: relative;
            padding: 1px 22px 3px 18px;
        }
    }

    .mkdf-new-product {
        @include mkdfWooCommerceNewProductStyle();

        .mkdf-new-text {
            position: relative;
        }

        .mkdf-new-svg {
            display: inline-block;
            position: relative;
            padding: 1px 22px 3px 18px;
        }
    }
}

.mkdf-woo-single-page {
    
    .mkdf-single-product-content {
        
        .mkdf-onsale,
        .mkdf-sold {
            left: -3px;
        }

        .mkdf-new-product {
            right: -5px;
        }
    }
    
    &.mkdf-woo-single-thumb-on-left-side {
        
        .mkdf-single-product-content {
            
            .mkdf-onsale,
            .mkdf-sold {
                left: 128px;
                right: auto;
            }

            .mkdf-new-product {
                right: -15px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */