/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include mkdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 3px;
            margin-top: 3px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 18px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #c8c8c8;
                height: 1px;
                position: relative;

                .ui-slider-handle {
                    cursor: pointer;
                    height: 11px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -5px;
                    width: 11px;
                    z-index: 2;

                    &:last-child {
                        transform: translateX(-50%);
                    }
                }

                .ui-slider-range {
                    background-color: $default-text-color;
                    display: block;
                    height: 1px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 15px 0 0;

            .button {
                float: left;
                padding-left: 40px;
                padding-right: 40px;

                @include mkdfButtonOutlineColor();

                &:hover {
                    @include mkdfButtonOutlineHoverColor();
                }
            }

            .price_label {
                float: right;
                color: $default-heading-color;
                position: relative;
                top: -3px;

                span {
                    font-family: "Amatic SC", sans-serif;
                    color: $first-main-color;
                    font-size: 25px;
                    position: relative;
                    top: 4px;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            padding: 17px 0 3px !important;

            li {
                min-height: $thumb;
                position: relative;
                box-sizing: border-box;

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        position: relative;
                    }

                    &:hover {

                        .product-title {
                            color: $first-main-color;
                            transition: color .2s ease-out;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {

        div {

            ul {

                li {
                    min-height: 70px;
                    padding-left: 84px;

                    a {
                        @include mkdfDefaultHeadingStyle();
                        @include mkdfH6();
                        margin: 2px 0;

                        &:not(.remove) {
                            display: block;
                            padding-right: 20px;
                        }
                    }

                    span.quantity {
                        @include mkdfDefaultHeadingStyle();
                        @include mkdfH6();
                        font-size: 15px;
                        color: $first-main-color;
                        margin: 4px 0 0 0;
                    }
                }
            }

            p {

                a {

                    &.button {
                        font-size: 18px !important;
                        background-image: none !important;
                        border: none;
                        padding: 0 !important;
                        color: $first-main-color !important;
                        background-color: transparent !important;

                        &:after {
                            padding-left: 7px;
                            position: relative;
                            top: 4px;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 5px;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include mkdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include mkdfButtonDefaultStyle();
                @include mkdfButtonSize();
                @include mkdfButtonSolidColor();

                &:hover {
                    @include mkdfButtonSolidHoverColor();
                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }

    &.widget_layered_nav {

        ul {

            li {

                span.count {
                    color: $default-heading-color;
                }
            }
        }
    }
}


/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */