/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include mkdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 20px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }

    &.widget_rating_filter {

        ul {

            li {
                margin: 0 0 10px;
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include mkdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 70px;
                        border-radius: 4px;
                        top: 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }

                    span.quantity {
                        display: block;
                    }
                }
            }
            
            .variation {
                @include mkdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
                color: $default-heading-color;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .mkdf-widget-title {
            margin: 0;
        }
        
        ul {
            
            li {
                margin: 9px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }

                &:last-child {
                    margin-top: 9px;
                }
            }
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 6px;
                background-color: #fff;
                border-radius: 4px;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 13px;
                    width: 14px;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 13" width="14" height="13"><path style="fill:%23f04336" d="M1 6.3c0 0-0.2-5.3 5.4-5.3S13 4.6 13 6.1s-1.2 5.7-5.9 5.8S1.2 8.2 1 6.3z"/></svg>');
                    background-repeat: no-repeat;
                    background-position: center;

                    &:last-child {
                        top: -9px;
                        height: 18px;
                        width: 20px;
                        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 18" width="20" height="18"><path style="fill:%23f04336" d="M10.3 6.5c2.1 0.3 2.9-1.5 3-2.1 0.1-0.6 0-2.4-2.6-2.7C8.3 1.3 8 3.6 8 3.6 8 4.4 8.2 6.2 10.3 6.5z"/><path style="fill:%23f04336" d="M17.2 6.6c-2.5-0.4-2.8 1.9-2.8 1.9 0 0.2 0 0.5 0.1 0.8C13.6 7.9 12 6.5 8.5 6.5c-5.7 0.1-5.4 5.3-5.4 5.3 0.2 1.9 1.4 5.8 6.1 5.7 4.7-0.1 5.9-4.3 5.9-5.8 0-0.3-0.1-0.8-0.2-1.3 0.3 0.5 0.9 0.9 1.8 1 2.1 0.3 2.9-1.5 3-2.1C19.8 8.6 19.7 6.9 17.2 6.6z"/><path style="fill:%23f04336" d="M4.6 3.7c-0.5-0.4-2-1.2-3.7 0.7s0.2 3.4 0.2 3.4c0.7 0.5 2.3 1.2 3.7-0.4C6.2 5.8 5.1 4.2 4.6 3.7z"/></svg>');
                        transform: translateX(-50%);
                    }

                    .mkdf-ms-explorer & {
                        background-color: $first-main-color;
                        width: 10px;
                        height: 10px;
                        top: -2px;
                        border-radius: 50%;

                        &:last-child {
                            background-color: $first-main-color;
                            width: 10px;
                            height: 10px;
                            top: -2px;
                            border-radius: 50%;
                        }
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $first-main-color;
                    z-index: 1;
                    border-radius: 4px;
                }
            }
        }
        
        .price_slider_amount {
            margin: 15px 0 0;
            
            .button {
                float: left;
                background-image: none !important;
                border: none;
                padding: 0;

                &:after {
                    position: relative;
                    display: inline-block;
                    padding-left: 7px;
                    top: 4px;
                    transition: .3s;
                }

                &:hover {
                    color: $first-main-color;
                    background-color: unset;
                    border: unset;

                    &:after {
                        transform: translateX(2px);
                    }
                }
            }
            
            .price_label {
                float: right;
            }
        }

        @include laptop-landscape-mac {

            .price_slider_amount {

                .button {
                    top: 1px;

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 0 0 18px;
                
                ul {
                    margin: 18px 0 0 18px;
                }
            }
        }

        > ul {
            padding-top: 8px;
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .mkdf-widget-title {
            margin: 0;
        }
        
        ul {
            
            li {
                min-height: 70px;
                padding-left: 84px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 70px;
                        border-radius: 4px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    display: inline-block;
                    margin-right: 100%;
                    font-size: 14px;
                    letter-spacing: 4px;
                    
                    span {
                        max-width: 83px;
                    }

                    .mkdf-ms-explorer & {
                        margin-right: 0;
                    }
                }
                
                del,
                ins,
                > span.woocommerce-Price-amount.amount {
                    @include mkdfDefaultHeadingStyle();
                    @include mkdfH6();
                    font-size: 15px;
                    display: inline;
                    margin: 0;
                    color: $first-main-color;

                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    color: $first-main-color;
                    text-decoration: none;
                }

                del {
                    color: #bebebe;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products,
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .product-title {
                    color: inherit;
                    @include mkdfDefaultHeadingStyle();
                    @include mkdfH6();
                    margin: 4px 0 0 0;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 3px;
                    margin-bottom: 0;
                }
            }
        }
        
        a {
            display: block;
        }
        
        .reviewer {
            font-size: 13px;
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 3px;
                    margin-bottom: 3px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }

    &.widget_product_tag_cloud {

        .tagcloud {

            a {

                &:after {
                    content: '/';
                    margin: 0 0 0 5px;
                }

                &:last-child {

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */