/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
	position: absolute;
	margin: 0 !important; //absolute centering over added item
	padding: 14px 28px 16px;
	line-height: inherit;
	white-space: nowrap;
	border: 1px solid #fff;
	border-radius: 4px;
	opacity: 0;
	transform: translate(-50%, -50%);
	transform-origin: (0 0);
	z-index: 99999;

	#yith-wcwl-message {
		font-family: $default-heading-font;
		font-size: 16px;
		font-weight: 700;
		color: $default-heading-color;
		
		&:before {
			position: relative;
			font-family: 'dripicons-v2';
			content: "\e004";
			top: 2px;
			right: 7px;
		}
	}
	
	.qodef-qw-is-opened & {
		position: fixed;
		top: 50%;
		left: 50%;
		opacity: 1;
	}

	&.mkdf-wishlist-vanish-out {
		animation: mkdfWishlistvanishOut 1.5s both;
	}
}

@keyframes mkdfWishlistvanishOut {
	0% {
		opacity: 1;
		-webkit-transform: scale(0.8, 0.8) translate(-50%, -50%);
		-moz-transform: scale(0.8, 0.8) translate(-50%, -50%);
		transform: scale(0.8, 0.8) translate(-50%, -50%);
	}
	20% {
		opacity: 1;
		-webkit-transform: scale(1, 1) translate(-50%, -50%);
		-moz-transform: scale(1, 1) translate(-50%, -50%);
		transform: scale(1, 1) translate(-50%, -50%);
	}
	80% {
		opacity: 1;
		-webkit-transform: scale(1, 1) translate(-50%, -50%);
		-moz-transform: scale(1, 1) translate(-50%, -50%);
		transform: scale(1, 1) translate(-50%, -50%);
	}
	100% {
		opacity: 0;
		-webkit-transform: scale(1.2, 1.2) translate(-50%, -50%);
		-moz-transform: scale(1.2, 1.2) translate(-50%, -50%);
		transform: scale(1.2, 1.2) translate(-50%, -50%);
	}
}

.qodef-yith-wcqv-holder {
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
	background-color: #fff;
	z-index: 5;
	border-radius: 4px 4px 0 0;
	margin: 0 2px;
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {
	
	a {
		position: relative;
		display: inline-block;
		vertical-align: top;
		font-size: 0;
		line-height: inherit;
		color: $first-main-color;
		padding: 6px 10px 5px;
		transition: color .2s ease-out, background-color .2s ease-out;
		
		&:after {
			font-family: 'dripicons-v2' !important;
			font-size: 20px;
			position: relative;
			display: inline-block;
			vertical-align: top;
			line-height: inherit;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}
		
		&:hover {
			background-color: $first-main-color;

			&:after {
				color: #fff;
			}
		}
	}
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

	a {
		color: #fff;
		background-color: $first-main-color;
		border-radius: 4px 4px 0 0;
	}
}

.yith-wcwl-add-button {

	.ajax-loading {
		display: none !important;
	}

	a {

		&:after {
			content: "\e004" !important;;
		}
	}

	a.add_to_wishlist {
		border-radius: 4px 4px 0 0;

		&:hover {
			
			&:after {
				animation: none !important;
			}
		}
	}
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {
	
	.feedback {
		display: none;
	}

	a {

		&:after {
			content: "\e004" !important;;
		}
	}
}

.woocommerce-wishlist {
	
	.wishlist-title {
		display: none;
	}
	
	.woocommerce-message,
	.woocommerce-info,
	.woocommerce-error {
		position: relative;
		display: block;
		padding: 15px 30px;
		margin: 0 0 50px;
		line-height: 48px;
		list-style: none;
		background-color: transparent;
		border: 1px solid $default-border-color;
		
		> a {
			color: $default-text-color;
			
			&:hover {
				color: $default-heading-color;
			}
		}
		
		a {
			float: right;
		}
	}
	
	table.wishlist_table {
		margin: 0 0 100px;
		font-size: inherit;
		
		thead {

			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;

				th {
					@include mkdfDefaultHeadingStyle();
					@include mkdfH5();
					padding: 10px 0;
					color: $default-heading-color;
					text-align: left;
					border: 0;
				}
			}
		}
		
		tbody {
			
			tr {
				border: 0;
				border-bottom: 1px solid $default-border-color;
				
				td {
					padding: 25px 0;
					vertical-align: middle;
					text-align: inherit;
					
					&.product-remove {
						padding-left: 10px;
						padding-right: 10px;
						
						a {
							color: $default-text-color;
							font-size: 0;
							line-height: 1;
							
							&:after {
								content: "\f404";
								display: inline-block;
								vertical-align: middle;
								speak: none;
								color: inherit;
								font-family: "Ionicons";
								font-size: 25px;
								line-height: inherit;
								font-style: normal;
								font-weight: normal;
								font-variant: normal;
								text-transform: none;
								text-rendering: auto;
								-webkit-font-smoothing: antialiased;
								-moz-osx-font-smoothing: grayscale;
							}

							&:hover {
								color: $default-heading-color;
							}
						}
					}
					
					&.product-thumbnail {
						width: 86px;
						padding-right: 10px;

						a, img {
							display: block;
						}
					}
					
					&.product-name {

						.yith-wcqv-button {
							height: 14px;
							padding: 0 15px;
							margin: 0;
							position: relative;
							top: 7px;

							&:hover {
								color: $first-main-color;
							}
						}
					}
					
					&.product-price {
						
						ins {
							text-decoration: none;
						}
					}
					
					&.product-stock-status span {
						color: $default-text-color !important;
					}
					
					&.product-add-to-cart {
						text-align: right;

						a {
							display: inline-block !important;
							margin: 0;
						}
					}
				}
				
				@include phone-landscape {
					
					td {
						
						&.product-name {
							width: 40%;
						}
						
						&.product-thumbnail,
						&.product-stock-status {
							display: none;
						}
					}
				}
			}
		}
		
		.yith-wcwl-share {
			margin: 0;
			display: none;
		}
	}
}

.mkdf-single-product-summary {
	
	.yith-wcwl-add-to-wishlist {
		position: relative;
		transform: none;
		border-radius: 0;
		left: 0;
        margin: 11px 0 0;
		
		.yith-wcwl-add-button,
		.yith-wcwl-wishlistaddedbrowse,
		.yith-wcwl-wishlistexistsbrowse {
			
			a {
				font-family: $default-heading-font;
				font-size: 18px;
				font-weight: 800;
				color: $first-main-color;
				border-radius: 0;
				padding: 0;
				background-color: transparent;

				&:hover {
					background-color: transparent;
				}

				&:after {
                    visibility: hidden;
					padding-left: 3px;
				}
			}
		}

		.yith-wcwl-add-button {

			a {

				&:after {
					padding-left: 8px;
				}
			}
		}
	}
}

.mkdf-wishlist-widget-holder {
	display: inline-block;
	vertical-align: middle;
	
	a {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		font-family: 'Nunito', sans-serif;
		font-weight: 700;
		font-size: 18px;
		line-height: 1;
		margin: 0 0 0 36px;
		color: $default-heading-color;

        .mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) & {
            color: #fff;

            &:hover {
                color: #fff;
            }
        }
		
		&:hover {
			color: $default-heading-color;

			svg {
				animation: mkdf-wiggle-icon-animation .5s forwards;
			}
		}

		> span {
			position: relative;
			top: -8px;
			padding-right: 9px;
		}

		svg.mkdf-wishlist {
			position: relative;
			bottom: 2px;
			display: inline-block;
            position: relative;
            transform-origin: center;

			path {
				fill:none;
				stroke: currentColor;
				stroke-miterlimit: 10;
			}
		}
	}
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */