/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce {
    @include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
    margin: 0 0 22px;
    
    .mkdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;

        ul.products {

            > .product {

                svg.mkdf-pl {
                    position: absolute;
                    width: 86%;
                    height: 90%;
                    top: 5%;
                    left: 7%;

                    path {
                        fill: #fff;
                    }
                }

                .mkdf-pl-text-inner {

                    a.button.add_to_cart_button,
                    a.button.product_type_simple,
                    a.button.product_type_grouped,
                    a.product_type_external,
                    a.added_to_cart {
                        background-image: none;
                        background-color: transparent;
                        font-size: 18px;
                        color: $first-main-color;
                        margin: 30px 0 0;
                        padding: 0 15px;
                        white-space: normal !important;

                        &:after {
                            padding-left: 8px;
                            position: relative;
                            top: 4px;
                        }
                    }

                    a.added_to_cart {
                        margin-top: 33px;
                    }

                    span.price {
                        padding-top: 0;
                        top: 19px;
                    }
                }

                .mkdf-product-list-title {
                    position: relative;
                    top: 10px;
                    margin: 0 0 4px;
                }
            }
        }
    }

    .mkdf-woo-pl-info-below-image & {

        .mkdf-pl-inner {

            > a {

                .touch & {
                    pointer-events: none;
                }
            }
        }
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
        box-sizing: border-box;
        
        .add_to_cart_button, .added_to_cart, .button.product_type_simple {
            transform: translateY(10px);
        }
        
        .touch & {
            cursor: pointer;

            > a {
                display: none;
            }
        }

        .mkdf-pl-inner {

            &:hover {

                .mkdf-pl-text {
                    opacity: 1;

                    .add_to_cart_button, .added_to_cart, .button.product_type_simple {
                        transform: translateY(0);
                    }
                }

                .mkdf-woo-pl-info-on-image-hover & {

                    .mkdf-pl-text {
                        border-radius: 9px;
                        background-color: $additional-background-color;
                    }
                }

                .mkdf-pl-text-wrapper {

                    .mkdf-product-list-title {
                        color: $first-main-color;
                    }
                }
            }
        }
        
        .mkdf-product-list-title {
            margin: 0 0 7px;
        }
        
        .mkdf-pl-rating-holder {
            @include mkdfRelativeHolderLayout();
            line-height: 1;
            margin: 0 0 12px;
        }
        
        .price {
            @include mkdfWooCommercePriceStyle();
            padding-top: 1px;
        }
        
        .button:not(.yith-wcqv-button),
        .added_to_cart {
            margin: 25px 0 0;
    
            .mkdf-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .mkdf-pl-inner {
            @include mkdfRelativeHolderLayout();
            
            .mkdf-pl-image {
                @include mkdfRelativeHolderLayout();
                
                img {
                    display: block;
                    width: 100%;
                    border-radius: 9px;
                }
            }
            
            .mkdf-pl-text {
                @include mkdfAbsoluteHolderLayout();
                padding: 0;
                opacity: 0;
                box-sizing: border-box;
                transition: opacity .2s ease-out;
                overflow: hidden;
            }
            
            .mkdf-pl-text-outer {
                @include mkdfTableLayout();
                text-align: center;
            }
            
            .mkdf-pl-text-inner {
                @include mkdfTableCellLayout();
                
                .mkdf-product-list-title {
                    color: $default-heading-color;
                }

                .mkdf-pl-rating-holder {
                    margin: 7px 0 -7px;
                }

                .star-rating {
                    color: #ffc72c;
                    font-size: 15px;
                    letter-spacing: 4px;
                }

                .price {
                    color: $first-main-color;

                    del {
                        margin: 0;
                    }
                }
            }
            
            > a:not(.yith-wcqv-button) {
                @include mkdfAbsoluteHolderLayout();
            }

            .mkdf-pl-yith-wrapper {
                width: 100%;
                display: flex;
                align-items: flex-end;
                justify-content: center;
                position: absolute;
                bottom: -2px;
            }
        }
        
        .mkdf-pl-text-wrapper {
            @include mkdfRelativeHolderLayout();
            margin: 21px 0 10px;
            text-align: center;

            .star-rating {
                font-size: 15px;
                letter-spacing: 4px;
            }
        }

        > a {
            @include mkdfAbsoluteHolderLayout();
            pointer-events: none;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */