/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					$thumb_size: 123;
					$space_between_items: 13;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: 142;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									overflow: hidden;
									border-radius: 6px;
									
									img {
										max-width: none;
										max-height: 100%;
										height: 100%;
										width: auto;
										display: block;
										position: relative;
										top: 50%;
										left: 50%;
										transform: translate(-50%, -50%);
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 53px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 59.6%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
						border-radius: 6px;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 40.4%;
        padding: 0 0 0 45px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 4px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 17px;

			.woocommerce-review-link {
				top: 2px;
				position: relative;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 30px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }

		.variations {

			.select2-container.select2-container--default {
				width: 230px !important;

				.select2-selection--single {
					background-color: $additional-background-color;
				}
			}
		}

		.single_variation_wrap {
			margin: 19px 0 0;
		}
	
	    .product_meta {
		    margin: 26px 0 0;
			color: $default-heading-color;
		
		    > span {
			    display: block;
			    margin: 3px 0 4px;
			
			    a {
					font-family: "Amatic SC", sans-serif;
					font-size: 25px;
				    color: $first-main-color;
				
				    &:hover {
					    color: $default-heading-color;
				    }

					&:first-child {
						margin-left: 16px;
					}
			    }
		    }

			span.sku {
				font-family: "Amatic SC", sans-serif;
				font-size: 25px;
				color: $first-main-color;
				margin-left: 16px;
			}
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 17px 0 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }

			.mkdf-social-title {
				margin-right: 16px;
				margin-top: 0px;
			}
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 36px 0 0;

			&.grouped_form {

				.product {
					margin: 0;
				}
			}
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 15px 15px 0;
			border-radius: 4px;
	    }

		button[type=submit] {

			&:not(.mkdf-search-submit) {

				&.single_add_to_cart_button.button.alt {
					margin-bottom: 15px;
                    padding: 17px 36px;
				}
			}
		}
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 70px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				border-radius: 6px 6px 0 0;

				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					@include mkdfDefaultHeadingStyle();
					@include mkdfH5();
					padding: 7px 12px 9px;
					margin: 17px 30px 9px;
					z-index: 10;
				}

				&.active {
					background-color: $additional-background-color;

					a {
						color: #fff;

						&:after {
							content: '';
							width: 100%;
							height: 100%;
							position: absolute;
							top: 0;
							left: 0;
							transform: rotate(-.8deg);
							background-color: $first-main-color;
							border-radius: 6px;
							z-index: -1;
						}
					}
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			background-color: $additional-background-color;
			padding: 56px 42px 58px;
			box-sizing: border-box;
			
			> h2 {
				display: none;
			}
			
			> p {
				padding: 4px 0;
				margin: 0;
			}
		}
		
		table {
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
                font-weight: 400;
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
                font-family: 'Amatic SC', sans-serif;
                font-size: 25px;
                color: $first-main-color;
                position: relative;
                top: -4px;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			box-sizing: border-box;
			
			h2 {
				@include mkdfH4();
				margin: 3px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 95px;
                        border-radius: 5px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 115px;
					
					p {
						margin: 0;
					}

                    .star-rating {
                        margin-bottom: 6px;
                    }

                    .description {
                        margin-top: 7px;
                    }
				}

                .woocommerce-review__author {
                    font-family: 'Nunito', sans-serif;
                    font-size: 18px;
                    font-weight: 900;
                    color: $default-heading-color;
                    margin-right: 7px;
                }

                .woocommerce-review__dash {
                    display: none;
                }

                .woocommerce-review__published-date {
                    font-family: 'Amatic SC', sans-serif;
                    color: $default-heading-color;
                    font-size: 20px;
                }
			}
			
			.comment-respond {
				margin-top: 35px;
				
				.comment-reply-title {
                    @include mkdfH5();
                    @include mkdfDefaultHeadingStyle;
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						left: 3px;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();

						&:before {
							letter-spacing: 2px;
							top: -3px;
							left: -3px;
						}
					}
				}

                .comment-form-cookies-consent {
                    margin-bottom: 25px;
                }
			}

			#respond textarea {
				background-color: #fff;
				margin-top: 16px;
				margin-bottom: 26px;

                &:focus {
                    border-color :transparent;
                }
			}

            #respond input[type=text],
            #respond input[type=email] {
                background-color: #fff;

                &:focus {
                    border-color :transparent;
                }
            }
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 82px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 35px;
			text-align: left;
			@include mkdfH4();
		}
	}
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */