/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        background-color: $additional-background-color;
        color: $default-heading-color;
        width: 222px;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            -webkit-user-select: none;
            box-sizing: border-box;
            font-family: "Amatic SC", sans-serif;
        }

        .mkdf-quantity-input {
            position: relative;
            float: right;
            height: 60px;
            width: 44px;
            padding: 0;
            margin: 0 29px 0 0;
            font-family: "Amatic SC", sans-serif;
            font-size: 25px;
            line-height: 42px;
            outline: 0;

            &:focus {
                border-color: transparent;
            }
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 29px;
            height: 30px;
            font-size: 35px;
            line-height: 20px;
            border-left: 0;
            cursor: pointer;
            transition: color .15s ease-in-out;
            padding-right: 15px;
            padding-top: 4px;

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
        }

        .mkdf-quantity-label {
            position: absolute;
            top: 50%;
            left: 27px;
            transform: translateY(-50%);
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */