/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 0 0 36px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .mkdf-sc-dropdown {
            visibility: visible;
            pointer-events: auto;
            opacity: 1;
            transform: translateY(0);
            transition: .4s cubic-bezier(0.16, 0.79, 0.3, 0.96);
        }
    }

    .mkdf-shopping-cart-inner {
        @include mkdfRelativeHolderLayout();
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .mkdf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            top: -3px;
            line-height: 1;
        }

        &.mkdf-header-cart-icon-pack {
            
            .mkdf-sc-opener-icon {
                font-size: 27px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }

                .mkdf-sc-opener-count-holder {
                    top: 0;
                    right: -9px;

                    span.mkdf-sc-opener-count {
                        top: 1px;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            
            .mkdf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }

        &.mkdf-header-cart-predefined {

            &:hover {
                svg {
                    animation: mkdf-wiggle-icon-animation .5s forwards;
                }
            }

            svg.mkdf-dropdown-cart {
                width: 32px;
                height: 31px;
                display: inline-block;
                position: relative;
                transform-origin: center;

                path {
                    fill: none;
                    stroke: currentColor;
                    stroke-miterlimit: 10;
                }
            }
        }

        .mkdf-sc-opener-count-holder {
            position: absolute;
            top: 3px;
            right: -8px;
            height: 15px;
            min-width: 15px;
            background-color: red;
            border-radius: 8px;
            text-align: center;

            .mkdf-sc-opener-count {
                position: relative;
                font-family: 'Nunito', sans-serif;
                font-size: 12px;
                font-weight: 600;
                color: #fff;
                text-align: center;
                padding: 0 4px;
                top: -1px;
            }
        }

        .mkdf-dropdown-cart-text {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-family: $default-heading-font;
            font-weight: 700;
            font-size: 18px;
            line-height: 1;
            margin: 0 15px 0 0;
            top: -1px;
        }
    }

    .mkdf-empty-cart {
        color: #4a4a4a;
        position: relative;
        top: -4px;
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-color;
    }
}

.mkdf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    text-align: left;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    pointer-events: none;
    transform: translateY(30px);
    transition: .1s;
    
    .mkdf-sc-dropdown-inner {
        @include mkdfRelativeHolderLayout();
        width: 274px;
        padding: 38px 33px 34px 38px;
        background-color: #fff;
        border: 1px solid #fff;
        box-sizing: border-box;
        border-radius: 5px;
        box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.1);
    }
    
    .mkdf-sc-dropdown-items {
        @include mkdfRelativeHolderLayout();
    }
    
    .mkdf-sc-dropdown-item {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin-top: 20px;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .mkdf-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 75px;
        height: 83px;
        margin-right: 14px;
        
        a, img {
            display: block;
            height: 100%;
        }

        img {
            object-fit: cover;
            border-radius: 3px;
        }
    }
    
    .mkdf-sc-dropdown-item-content {
        @include mkdfRelativeHolderLayout();
        padding-right: 20px;
        top: -3px;
    }
    
    .mkdf-sc-dropdown-item-title {
        @include mkdfH6();
        margin: 0;
    }
    
    .mkdf-sc-dropdown-item-price {
        @include mkdfDefaultHeadingStyle();
        font-size: 15px;
        line-height: 19px;
        color: $first-main-color;
        margin: 2px 0 0 0;
    }
    
    .mkdf-sc-dropdown-item-quantity {
        margin: 1px 0 0;
        color: $default-heading-color;
    }
    
    .mkdf-sc-dropdown-item-remove {
        position: absolute;
        top: -1px;
        right: 0;
        font-size: 20px;
        line-height: 1;

        svg.mkdf-dropdown-remove {
            width: 11px;
            height: 12px;

            path {
                fill: $first-main-color;
            }
        }
    }
    
    .mkdf-sc-dropdown-subtotal {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
        
        > * {
            margin: 0;
        }

        h6.mkdf-sc-dropdown-total {
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: 400;
            position: relative;
            top: -1px;
        }

        .mkdf-sc-dropdown-total-amount {
            font-family: $default-heading-font;
            font-weight: 900;
            color: $default-heading-color;
        }
    }
    
    .mkdf-sc-dropdown-button-holder {
        @include mkdfRelativeHolderLayout();
        margin-top: 10px;

        a {
            &:first-child {
                margin-right: 29px;
                float: left;
            }
            &:last-child {
                float: right;
            }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */