/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.mkdf-plc-holder {
    @include mkdfRelativeHolderLayout();

    .mkdf-plc-outer {
        @include mkdfRelativeHolderLayout();
    }

    .mkdf-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        &:hover {

            .mkdf-plc-image-outer {

                .mkdf-plc-text {
                    opacity: 1;
                }
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-plc-image-outer {
            @include mkdfRelativeHolderLayout();

            .mkdf-plc-image {
                @include mkdfRelativeHolderLayout();

                .mkdf-plc-onsale,
                .mkdf-plc-out-of-stock,
                .mkdf-plc-new-product {
	                @include mkdfWooCommerceMarksGeneralStyle();
                }

                .mkdf-plc-onsale {
	                @include mkdfWooCommerceSaleStyle();
                    left: 15px;

                    .mkdf-onsale-text {
                        position: relative;
                    }

                    .mkdf-onsale-svg {
                        display: inline-block;
                        position: relative;
                        padding: 1px 22px 3px 18px;
                    }
                }

                .mkdf-plc-out-of-stock {
	                @include mkdfWooCommerceOutOfStockStyle();
                    left: 15px;

                    .mkdf-sold-text {
                        position: relative;
                    }

                    .mkdf-sold-svg {
                        display: inline-block;
                        position: relative;
                        padding: 1px 22px 3px 18px;
                    }
                }
	
	            .mkdf-plc-new-product {
		            @include mkdfWooCommerceNewProductStyle();
                    right: 15px;

                    .mkdf-new-text {
                        position: relative;
                    }

                    .mkdf-new-svg {
                        display: inline-block;
                        position: relative;
                        padding: 1px 22px 3px 18px;
                    }
	            }
            }

            .mkdf-plc-text {
                position: absolute;
                top: 0;
                left: 0;
                display: inline-block;
                width: 100%;
                height: 100%;
                padding: 10px;
                opacity: 0;
                background-color: rgba(#fff, 0.7);
                box-sizing: border-box;
                transition: opacity .2s ease-out, background-color .2s ease-out;
            }

            .mkdf-plc-text-outer {
                position: relative;
                display: table;
                width: 100%;
                height: 100%;
                text-align: center;
            }

            .mkdf-plc-text-inner {
                position: relative;
                display: table-cell;
                width: 100%;
                height: 100%;
                vertical-align: middle;
            }
        }

        .mkdf-plc-title {
            margin: 0;
        }
	
	    .mkdf-plc-category {
            @include mkdfDefaultHeadingStyle();
            @include mkdfH6();
		    margin: 0;
	    }

        .mkdf-plc-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 12px 0 4px;
        }

        .mkdf-plc-rating-holder {
            @include mkdfRelativeHolderLayout();
            line-height: 1;
            margin: 5px 0 5px;
        }

        .mkdf-plc-rating {
	        @include mkdfWooCommerceRatingsStyle();
        }

        .mkdf-plc-price {
	        @include mkdfWooCommercePriceStyle();
            margin-top: 10px;
        }
	
