	    .button.added {
		    display: none;
	    }
	
	    .mkdf-plc-add-to-cart {
		    margin: 20px 0 0;
		
		    &.mkdf-default-skin {
			
			    .button,
			    .added_to_cart {
				    @include mkdfButtonSolidColor();
				
				    &:hover {
					    @include mkdfButtonSolidHoverColor();
				    }
			    }
		    }
		
		    &.mkdf-light-skin {
			
			    .button,
			    .added_to_cart {
				    color: $default-heading-color;
				    background-color: #fff;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
		
		    &.mkdf-dark-skin {
			
			    .button,
			    .added_to_cart {
				    color: #fff;
				    background-color: $default-heading-color;
				
				    &:hover {
					    color: #fff;
					    background-color: $first-main-color;
				    }
			    }
		    }
	    }

        .mkdf-plc-link {
            @include mkdfAbsoluteHolderLayout();
        }
    }

    &.mkdf-standard-layout {

        .mkdf-plc-item {
    
            .mkdf-plc-title,
            .mkdf-plc-category a,
            .mkdf-plc-excerpt {
                color: $default-heading-color;
            }

            .mkdf-plc-rating {
                color: #ffc72c;
            }

            .mkdf-plc-price {
                color: $first-main-color;

                del {
                    color: $default-text-color;
                }
            }
	
	        .mkdf-plc-category a:hover {
		        color: rgba(#fff, .8);
	        }
        }
    }

    &.mkdf-simple-layout {
        
        .mkdf-plc-image-outer {
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 15px 0 0;
        }

        .mkdf-plc-text-wrapper {
            position: relative;
            display: table-cell;
            vertical-align: middle;
            width: 50%;
            padding: 0 0 0 15px;
        }

        .mkdf-plc-text,
        .mkdf-plc-text-outer,
        .mkdf-plc-text-inner {
            @include mkdfRelativeHolderLayout();
            -webkit-transform: translateZ(0);
        }
    }
	
	&.mkdf-plc-pag-inside-slider {
		
		.owl-prev,
		.owl-next {
			transform: translateY(-50%);
		}
		
		.owl-dots {
			position: absolute;
			left: 13px;
			bottom: 10px;
			margin: 0;
            z-index: 999;
		}
	}
	
	&.mkdf-plc-pag-light-skin {
		
		.owl-dots {
			
			.owl-dot {

				span {
					background-color: #fff;
                    border-color: #fff;
				}

				&:hover,
				&.active {

					span {
						background-color: $first-main-color;
					}
				}
			}
		}
	}
}
