/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */

#yith-quick-view-modal.open,
#yith-quick-view-modal {

	.yith-quick-view-overlay {
		background-color: rgba(#fff, .77);
	}

	.yith-wcqv-wrapper {
		padding: 0;
		max-width: 1080px;
	}

	.yith-wcqv-main {
		@include mkdfRelativeHolderLayout();
		padding: 0;
		background-color: #fff;
		box-shadow: 0 0 18px rgba(#000, .1);
		box-sizing: border-box;
		border-radius: 19px;
		overflow: hidden;
	}

	#yith-quick-view-content {
        height: 100%;

		.woocommerce-product-gallery__wrapper {
            height: 100%;
			margin: 0 -6px;

			div {
                height: 100%;
				padding: 0 6px;
				box-sizing: border-box;
			}

			.mkdf-onsale,
			.mkdf-sold {
				left: 22px;
			}

			.mkdf-new-product {
				right: 18px;
			}
		}

		.product {
            height: 100%;
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0;
		}

		.product-type-external {

			.summary {

				.mkdf-single-product-share-wish {
					margin-top: 47px;
				}
			}
		}

		.product-type-variable {

			form.variations_form.cart {
				margin: 19px 0 0;
			}
		}

		.images,
		.summary {
			position: relative;
			float: left;
			box-sizing: border-box;
		}

		.images {
			width: 50%;
            height: 100%;

			a, img {
				display: block;
			}

			a {
				pointer-events: none;
				height: 100%;
				overflow: hidden;
			}

			img {
                height: 100%;
                object-fit: cover;
				display: block;
			}
		}

		.summary {
			width: 50%;
			padding: 52px 53px 59px;

			.variations {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 0 20px;
				padding: 0;
				border: none;

				select {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					padding: 0 16px 0 16px;
					margin: 0 6px 0 0;
					height: 47px;
					font-family: $default-text-font;
					font-size: inherit;
					line-height: 47px;
					font-weight: inherit;
					color: $default-text-color;
					background-color: #fff;
					border: 1px solid #e1e1e1;
					box-sizing: border-box;
				}

				tr {
					display: block;
					float: left;
					border: 0;
					margin-bottom: 5px;

					&:first-child {
						padding-right: 6px;
					}
				}

				td {
					display: block;
					padding: 0;
					text-align: left;
				}

				td.label {
					width: 12%;
				}

				.select2-container {
					min-width: 180px;
				}

				.reset_variations {
					position: relative;
					top: -5px;
					right: -10px;
					color: $default-heading-color;
				}
			}

			.single_variation_wrap {
				margin: 3px 0 0;

				.single_variation {
					margin: 0 0 35px;
				}
			}

			table.group_table {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0;
				padding: 0;
				border: none;

				tbody {
					display: block;
				}

				tr {
					display: table;
					width: 100%;
					margin-bottom: 13px;
					background-color: #fff;
					border: none;
				}

				td {
					display: inline-block;
					padding: 0 5px;
					text-align: left;
					box-sizing: border-box;

					&:first-child {
						padding: 0;
					}

					&.woocommerce-grouped-product-list-item__label,
					span.woocommerce-Price-amount.amount {
						position: relative;
						top: -6px;
					}

					&.price {
						display: table-cell;
						margin: 0;
						vertical-align: middle;
						font-size: 16px;
						text-align: center;

					}

					.mkdf-quantity-buttons {

						.mkdf-quantity-input {
							height: 52px;
						}
					}
				}

				a {
					color: $default-text-color;

					&:hover {
						color: $default-heading-color;
					}
				}
			}

			.mkdf-yith-product-title {
				@include mkdfH3();
				display: block;
		        margin: 0 0 4px;
			}

			div[itemprop="offers"] {
				position: relative;
				display: block;
			}

			.price {
				@include mkdfWooCommercePriceStyle();
			}

			.woocommerce-product-rating {
				@include mkdfRelativeHolderLayout();
			    line-height: 1;
			    margin: 0 0 17px;
			}

			div[itemprop="description"],
			.woocommerce-product-details__short-description {
				@include mkdfRelativeHolderLayout();
			    margin: 26px 0 0;

			    p {
				    margin: 0;
			    }
			}

			.cart {
				@include mkdfRelativeHolderLayout();
		        margin: 36px 0 0;
			}

			.mkdf-quantity-buttons {
				margin: 0 15px 15px 0;
				border-radius: 4px;
				width: 194px;
		    }

