			.mkdf-single-product-share-wish {
				position: relative;
				display: inline-block;
				width: 100%;
				vertical-align: middle;
				margin: 0;

				.yith-wcwl-wishlistexistsbrowse,
				.yith-wcwl-wishlistaddedbrowse {

					a {

						&:after {
							color: $first-main-color;
						}
					}
				}

				.clear {
					display: none;
				}

				> div {
					padding: 0 20px;

					&:after {
						content: "|";
						position: absolute;
						left: 0;
						top: 0;
						color: #d3d3d3;
					}

					&:first-child {
						padding-left: 0;

						&:after {
							display: none;
						}
					}

					&:last-child {
						padding-right: 0;
					}
				}

				.mkdf-woo-social-share-holder {
					position: relative;
					float: left;

					> * {
						display: table-cell;
						vertical-align: top;
						line-height: 22px;
					}

					> span {
						font-size: 12px;
						text-transform: uppercase;
						letter-spacing: 2px;
						color: $first-main-color;
						padding: 0 9px 0 0;
					}
				}
			}

			.yith-wcwl-add-to-wishlist {
				position: initial;
				left: 0;
				display: inline-block;
				transform: none;
				margin: 12px 0 0;
				background-color: transparent;

				.yith-wcwl-add-button,
				.yith-wcwl-wishlistaddedbrowse,
				.yith-wcwl-wishlistexistsbrowse {

					a {
						font-family: $default-heading-font;
						font-size: 18px;
						font-weight: 900;
						color: $first-main-color;
						border-radius: 0;
						padding: 0;
						background-color: transparent;

						&:hover {
							background-color: transparent;
						}

						&:after {
							padding-left: 8px;
						}
					}
				}

				.yith-wcwl-wishlistaddedbrowse,
				.yith-wcwl-wishlistexistsbrowse {

					a {

						&:after {
                            visibility: hidden;
							padding-left: 3px;
						}
					}
				}
			}

			p.stock.out-of-stock,
			p.stock.in-stock {
				margin: 22px 0 11px;
                font-weight: 700;
                color: $default-heading-color;
			}
		}
	}

