a.button.yith-wcqv-button {
	font-size: 0;
	height: 27px;
	margin: 0 2px;
	padding: 5px 8px 4px;
	color: $first-main-color;
	background-color: #fff;
	z-index: 5;
	border-radius: 4px 4px 0 0;
	transition: color .2s ease-out, background-color .2s ease-out;

	&:after {
